.jackpot {
  padding-top: 80px;
  padding-bottom: 60px;
  margin-top: 80px;
  background-color: $MainColor;
  .img {
    img {
      width: 100%;
    }
    span {
      margin-top: 40px;
      display: block;
      text-align: center;
      a {
        color: $WightColor;
        text-decoration: underline;
        @include transition;
        &:hover {
          color: $PinkColor;
        }
      }
    }
  }
  .boxses {
    .box {
      width: 100%;
      padding: 25px 15px 15px 15px;
      background-color: $boxColor;
      margin-bottom: 20px;
      .img {
        img {
          border-radius: 50%;
        }
      }
      .text {
        h4 {
          font-weight: bold;
          color: $WightColor;
        }
        p {
          font-size: 14px;
        }
      }
    }
  }
}
@media (max-width: 991px) {
  .jackpot {
    .img {
      margin-bottom: 60px;
    }
    .boxses {
      .box {
        .img {
          margin-bottom: 0;
          width: 20%;
          padding: 0;
        }
        .text {
          width: 78%;
        }
      }
    }
  }
}
/* Start Onlin Games Slider*/

.games-slider {
  padding-top: 80px;
  padding-bottom: 80px;
  .slider {
    margin-top: 40px;
    .slide {
      h3 {
        font-size: 24px;
        font-weight: bold;
        color: $WightColor;
      }
    }
  }
}

/* Start Referral Bonus Level */

.bouns-level {
  padding-top: 80px;
  padding-bottom: 80px;
  .work-proses {
    margin-top: 50px;
    .proses {
      i {
        font-size: 40px;
        color: #f39d36;
      }
      .number {
        position: relative;
        margin-bottom: 30px;
        background-color: $boxColor;
        width: 120px;
        height: 110px;
        line-height: 120px;
        margin: auto auto 30px auto;
        border-radius: 50%;

        &::before {
          font-size: 14px;
          font-weight: bold;
          position: absolute;
          top: 0px;
          left: 0px;
          width: 40px;
          height: 40px;
          line-height: 40px;
          border-radius: 50%;
        }
      }
      .number-yallo {
        &::before {
          content: "01 ";
          background-color: #f39d36;
        }
      }
      .number-blue {
        &::before {
          content: "02 ";
          background-color: #0098d7;
        }
      }
      .number-green {
        &::before {
          content: "03 ";
          background-color: #1aa15f;
        }
      }

      .blue {
        color: #0098d7;
      }
      .green {
        color: #1aa15f;
      }
      h4 {
        color: $WightColor;
        font-weight: bold;
        margin: 0 25px;
        line-height: 35px;
      }
    }
  }
}

@media (max-width: 991px) {
  .bouns-level {
    .work-proses {
      .proses {
        margin-bottom: 50px;
      }
    }
  }
}
