.login-form {
  width: 50%;
  margin: 50px auto;
  padding: 40px;
  background-color: $boxColor;
  border-radius: 4px;
  h4 {
    color: $WightColor;
    text-align: center;
    font-weight: bold;
    margin-bottom: 30px;
  }
  .input {
    display: block;
    width: 100%;
    height: 60px;
    padding: 7px 15px;
    font-size: 16px;
    line-height: 1.42857;
    color: rgb(255, 255, 255);
    background-color: transparent;
    background-image: none;
    border: 1px solid rgb(102, 102, 102);
    border-radius: 3px;
    margin-bottom: 20px;
  }
  .check-group {
    position: relative;
    margin: 10px 0px 20px;
    .check-box {
      color: rgb(68, 68, 68);
      font-weight: 300;
      letter-spacing: 0px;
      margin-bottom: 0px;
      outline: none;
      display: inline-flex;
      -webkit-box-align: center;
      align-items: center;
      padding-left: 0px;
      margin-right: 15px;
      input {
        margin: 4px 0 0;
        margin-top: 1px \9;
        line-height: normal;
      }
      span {
        color: rgb(196, 213, 246);
        font-weight: 300;
        font-size: 16px;
        left: 25px;
        position: absolute;
        top: 0px;
      }
    }
    a {
      font-size: 14px;
      float: right;
      margin-top: 2px;
      color: rgb(196, 213, 246);
      text-decoration: none;
      &:hover {
        color: #fff;
      }
    }
  }
  button {
    width: 100%;
    margin: 15px 0 30px;
    padding: 14px 0;
    border-radius: 0;
  }
  .or {
    text-align: center;
    color: $WightColor;
    font-weight: bold;
    margin-bottom: 25px;
  }
  ul {
    padding: 0;
    margin: 0;
    text-align: center;
    color: $WightColor;
    margin-bottom: 25px;
    li {
      display: inline-block;
      color: #fff;
      text-transform: uppercase;
      font-size: 14px;
      padding: 6px 15px;
      cursor: pointer;
    }
    .FACEBOOK {
      background-color: #3b5998;
    }
    .TWITTER {
      background-color: #00aced;
    }
    .GOOGLE {
      background-color: #c32f10;
    }
  }
  .account {
    text-align: center;
    color: $PargraphColor;
    font-size: 15px;
    font-weight: 600;
    a {
      color: $WightColor;
      font-weight: bold;
      text-decoration: none;
    }
  }
}

@media (max-width: 1000px) {
  .login-form {
    width: 90%;
  }
}
