.Win-Reward {
  background-color: $MainColor;
  color: $WightColor;
  padding: 80px 0;
  width: 100%;
  .box {
    width: 32%;
    text-align: center;
    background-color: $boxColor;
    padding: 35px 20px;
    font-weight: bold;
    position: relative;
    margin-right: 2%;
    &:last-of-type {
      margin-right: 0;
    }
    span {
      position: absolute;
      left: 0px;
      top: 10px;
      background: #0ec929;
      color: #fff;
      font-weight: 600;
      font-size: 18px;
      padding: 2px 15px;
      line-height: 28px;
      border-radius: 0px 3px 3px 0px;
    }
    img {
      border-radius: 50%;
      padding: 10px;
      border: 1px solid #666;
      margin: 20px 0;
    }
    h5,
    h4 {
      font-weight: bold;
      margin-bottom: 25px;
    }
    h5 {
      font-size: 18px;
    }
    p {
      font-size: 17px;
    }
    h4 {
      font-size: 20px;
    }
    button {
      font-weight: 6px 13px;
      font-size: 13px;
    }
  }
}
.Lottery-Numbr {
  padding: 80px 0;
  .boxses {
    width: 100%;
    margin-top: 50px;
    .box {
      width: 49%;
      margin-bottom: 2%;
      background-color: $boxColor;
      color: $WightColor;
      font-weight: bold;
      padding: 35px;
      position: relative;
      border-radius: 4px;
      .top-price {
        position: absolute;
        left: 0px;
        top: 10px;
        background: #0ec929;
        color: #fff;
        font-weight: 600;
        font-size: 18px;
        padding: 2px 15px;
        line-height: 28px;
        border-radius: 0px 3px 3px 0px;
      }
      .Win {
        text-align: center;
        h4 {
          font-weight: bold;
        }
        p {
          margin-top: 25px;
        }
        button {
          margin-top: 15px;
          padding: 6px 13px;
          font-size: 13px;
        }
      }
      hr {
        background-color: #666;
      }
      .Numbers {
        .number-top {
          position: relative;
          margin-top: 30px;
          color: $PargraphColor;
          input {
            position: absolute;
            top: 20%;
          }
          label {
            padding-left: 40px;
            font-weight: 400;
            font-size: 17px;
            font-weight: bold;
          }
          .genreted-text {
            display: block;
            margin-bottom: 15px;
            font-size: 13px;
          }
        }
        .number-all {
          margin-bottom: 15px;
          .number-serial {
            list-style: outside none none;
            margin: 0;
            padding: 0;
            li {
              display: inline-block;
              color: #fff;
              border: 1px solid #fff;
              border-radius: 50%;
              font-size: 13px;
              font-weight: 700;
              width: 30px;
              height: 30px;
              text-align: center;
              line-height: 30px;
              margin: 3px 1px;
              cursor: pointer;
            }
            .active {
              border: $PinkColor;
            }
          }
        }
        .self-ticket {
          .text {
            color: #fff;
            float: left;
            margin-right: 30px;
            line-height: 30px;
          }
          .active-num {
            span {
              display: inline-block;
              color: #fff;
              background: #d5057f;
              border: 1px solid #d5057f;
              border-radius: 50%;
              font-size: 13px;
              font-weight: 700;
              width: 30px;
              height: 30px;
              text-align: center;
              line-height: 30px;
              margin: 0px 3px;
              cursor: pointer;
            }
          }
        }
      }
    }
    .box-marg {
      margin-right: 2%;
    }
  }
}

@media (max-width: 991px) {
  .Win-Reward {
    .box {
      width: 100%;
      margin-bottom: 50px;
      margin-right: 0;
    }
  }
  .Lottery-Numbr {
    .head-parg {
      margin-bottom: 50px;
    }
    .boxses {
      margin: auto;
      .box {
        width: 100%;
        margin-bottom: 4%;
      }
      .box-marg {
        margin-right: 0%;
      }
    }
  }
}
