.price {
  background-color: $MainColor;
  padding: 80px 0 60px 0;
  .boxses {
    margin-right: -45px;
    margin-left: 0;
    .chips {
      background-color: $boxColor;
      width: 30%;
      margin-right: 4%;
      margin-bottom: 3%;
      padding: 35px 30px;
      position: relative;
      &:last-of-type {
        margin-right: 0%;
      }
      .price {
        position: absolute;
        right: 0px;
        top: 10px;
        background: #0ec929;
        color: #fff;
        font-weight: 600;
        font-size: 15px;
        padding: 2px 15px;
        line-height: 26px;
        border-radius: 3px 0px 0px 3px;
      }
      .img {
        width: 100%;
        border: 1px solid #ffffff42;
        padding-top: 12px;
        padding-bottom: 12px;
        border-radius: 50%;
        text-align: center;
        img {
          width: 60px;
          height: 60px;
        }
      }
      .text {
        padding-top: 7px;
        padding-left: 18px;
        h4 {
          color: $WightColor;
          font-weight: bold;
        }
      }
      button {
        width: 100px;
        font-size: 13px;
        padding: 3px;
      }
    }
  }
}
@media (max-width: 1198px) {
  .price {
    .boxses {
      .chips {
        .img {
          padding-left: 7px;
        }
      }
    }
  }
}
@media (max-width: 991px) {
  .price {
    .boxses {
      text-align: center;
      .chips {
        width: 47%;
        margin-right: 5%;
        .img {
          width: 50%;
          margin: auto auto 20px auto;
        }
        .text {
          h4 {
            margin-bottom: 20px;
          }
        }
        button {
          width: 130px;
          font-size: 14px;
          padding: 8px;
        }
      }
    }
  }
}

@media (max-width: 769px) {
  .price {
    .boxses {
      text-align: center;
      margin-right: -30px;
      .chips {
        width: 100%;
        margin-right: 0;
        text-align: left;
        .img {
          width: 20%;
          margin-bottom: 0;
        }
        .text {
          width: 75%;
          margin-top: 0;
        }
      }
    }
  }
}
@media (max-width: 575px) {
  .price {
    .boxses {
      margin-right: 0px;
    }
  }
}
