.arrow-scrool {
  width: 40px;
  height: 40px;
  line-height: 40px;
  font-size: 20px;
  text-align: center;
  position: fixed;
  right: 30px;
  bottom: 40px;
  color: $WightColor;
  background-color: #42406d;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  z-index: 100000;
  @include transition;
  &:hover {
    background-color: $PinkColor;
  }
}
