.contact {
  background-color: $MainColor;
  padding: 100px 0;
  .contact-box {
    margin-top: 70px;
    .single-icon {
      background-color: $boxColor;
      padding: 40px 30px;
      margin-bottom: 30px;
      width: 31%;
      margin-right: 2%;
      text-align: center;
      border-radius: 5px;
      position: relative;
      i {
        font-size: 40px;
        margin-bottom: 20px;
        color: #fff;
        display: inline-block;
        border: 1px solid #fff;
        width: 80px;
        height: 80px;
        line-height: 78px;
        border-radius: 50%;
      }
      p {
        font-size: 18px;
        line-height: 30px;
        margin-bottom: 0px;
        font-weight: 600;
      }
      &::before {
        content: "";
        position: absolute;
        background-color: $PinkColor;
        top: 0;
        left: 25%;
        width: 50%;
        height: 2px;
      }
    }
  }
  .img-form {
    margin-top: 30px;
    margin-right: 30px;
    .img {
      img {
        width: 100%;
      }
    }
    form {
      margin-top: 30px;
      .row {
        margin-left: 0;
      }
      .f-name {
        width: 49%;
        margin-right: 2%;
        input {
          border: 1px solid $boxColor;
          border-radius: 0;
          height: 46px;
          margin-bottom: 15px;
          padding: 20px;
          width: 100%;
          background: transparent;
          color: $PargraphColor;
        }
      }
      .l-name {
        width: 49%;
        input {
          border: 1px solid $boxColor;
          border-radius: 0;
          height: 46px;
          margin-bottom: 15px;
          padding: 20px;
          width: 100%;
          background: transparent;
          color: $PargraphColor;
        }
      }
      .email {
        width: 100%;
        input {
          border: 1px solid $boxColor;
          border-radius: 0;
          height: 46px;
          margin-bottom: 15px;
          padding: 20px;
          width: 100%;
          background: transparent;
          color: $PargraphColor;
        }
      }
      .Massage {
        width: 100%;
        textarea {
          border: 1px solid $boxColor;
          border-radius: 0;
          padding: 20px;
          height: 200px;
          width: 100%;
          background: transparent;
        }
        input {
          background: $PinkColor;
          color: $WightColor;
          display: block;
          font-size: 16px;
          margin-top: 5px;
          padding: 9px 40px;
          transition: 0.4s;
          border-radius: 2px;
          width: auto !important;
          margin-top: 20px;
          border: 1px solid #d5057f;
          text-transform: capitalize;
          font-weight: 700;
          transition: 0.4s;
          &:hover {
            color: $PinkColor;
            background-color: transparent;
          }
        }
      }
    }
  }
}

@media (max-width: 991px) {
  .contact {
    .contact-box {
      .single-icon {
        width: 70%;
        margin: auto auto 50px auto;
      }
    }
  }
}

@media (max-width: 767px) {
  .contact {
    .contact-box {
      .single-icon {
        width: 100%;
      }
    }
  }
}
