.games-history {
  padding-top: 80px;
  padding-bottom: 80px;
  background-image: url("../image/homes/games-history/s1.png");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  z-index: 1;
  &::after {
    background: $MainColor;
    opacity: 0.4;
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  .table {
    margin-top: 50px;
    color: $PargraphColor;
    table {
      width: 100%;
      tr {
        text-align: left;
        td,
        th {
          border-top: none;
          padding: 30px 10px 30px 30px;
        }
        td {
          padding: 25px 10px 25px 30px;
          font-size: 15px;
          font-weight: bold;
        }
        th {
          padding: 30px 10px 30px 30px;
        }
      }
      tr:nth-child(odd) {
        background-color: $boxColor;
      }
      tr:nth-child(even) {
        background-color: $MainColor;
      }
      .img {
        margin-right: 15px;
        margin-left: 15px;
        border-radius: 50%;
        margin-top: -5px;
        img {
          border-radius: 50%;
        }
      }
      .name {
        font-size: 17px;
        font-weight: bold;
      }
    }
  }
}

@media (max-width: 991px) {
  .table {
    width: 100%;
    overflow-x: scroll;
    table {
      tr {
        td:first-of-type {
          display: flex;
          flex-direction: column;
          .img {
            text-align: center;
          }
          .name {
            font-size: 14px;
            text-align: center;
            margin-top: 5px;
          }
        }
      }
    }
  }
}
@media (min-width: 991px) {
  .table {
    table {
      margin-left: -15px;
    }
  }
}
