.Our-Team-Member {
  padding: 80px 0;
  background-color: $MainColor;
  .margin {
    margin-bottom: 40px;
  }
  .member {
    width: 24%;
    margin-right: 1.2%;
    background-color: $boxColor;
    padding: 30px;
    margin-top: 1%;
    text-align: center;
    .img {
      border-radius: 50%;
      margin: auto;
      img {
        width: 140px;
        height: 140px;
        border-radius: 50%;
      }
    }
    h4 {
      color: $WightColor;
      font-weight: bold;
      margin-top: 20px;
    }
    p {
      margin-top: 20px;
      font-weight: bold;
    }
    .icon {
      margin-top: 20px;
      i {
        margin-right: 15px;
        color: $WightColor;
        cursor: pointer;
        transition: ease-in-out 0.2s;
        &:hover {
          color: $PinkColor;
        }
      }
    }
  }
  .no-margin {
    margin-right: 0;
  }
}

@media (max-width: 991px) {
  .Our-Team-Member {
    .member {
      width: 48%;
      margin-right: 2%;
      margin-bottom: 1%;
    }
  }
}

@media (max-width: 500px) {
  .Our-Team-Member {
    .member {
      width: 100%;
      margin-right: 0;
      margin-bottom: 2%;
    }
  }
}
