.Membership-Pricing {
  padding: 80px 0;
  .pricing-content {
    margin-top: 40px;
    .table-list {
      text-align: center;
      transition: all 0.4s ease 0s;
      background: $boxColor;
      border-radius: 5px;
      position: relative;
      overflow: hidden;
      border: 1px solid $boxColor;
      padding-bottom: 30px;
      margin-bottom: 30px;
      .base {
        position: absolute;
        right: -37px;
        top: 33px;
        background: #1fc157;
        width: 180px;
        text-align: center;
        height: 30px;
        line-height: 30px;
        color: #fff;
        transform: rotate(45deg);
        z-index: 2;
        text-transform: uppercase;
        font-size: 13px;
        font-weight: 600;
      }
      .top-price-inner {
        position: relative;
        text-align: center;
        padding: 40px 30px 0px;
        background: #2c2b4a;
        margin-bottom: 80px;
        z-index: 1;
        transition: all 0.4s ease 0s;
        h4 {
          color: $WightColor;
          font-size: 24px;
          font-weight: bold;
          text-transform: capitalize;
          margin-bottom: 30px;
          letter-spacing: 1px;
        }
        .rates {
          .prices {
            font-size: 40px;
            font-weight: 600;
            color: $WightColor;
            padding-right: 10px;
            .dolar {
              font-size: 24px;
              color: $WightColor;
              margin-right: 5px;
              position: relative;
              top: -17px;
            }
          }
          .users {
            color: #fff;
            text-transform: uppercase;
            font-size: 13px;
            font-weight: 700;
          }
        }
        &::after {
          border-top: 99px solid #2c2b4a;
          border-left: 140px solid transparent;
          border-right: 140px solid transparent;
          height: 50px;
          width: 100%;
          position: absolute;
          content: "";
          z-index: -1;
          left: 0;
          bottom: -55px;
          transition: all 0.4s ease 0s;
        }
      }
      ol {
        margin-top: 10px;
        list-style: none;
        padding: 0;
        li {
          padding: 8px 0px;
          position: relative;
          text-align: center;
          font-weight: 400;
          i {
            font-size: 18px;
            position: absolute;
            left: 35px;
            top: 9px;
            color: #1fc157;
          }
        }
        .cross {
          i {
            color: #ff5b5b;
          }
        }
      }
      button {
        font-size: 14px;
        padding: 12px 34px;
      }
    }
  }
}

@media (max-width: 991px) {
  .Membership-Pricing {
    .pricing-content {
      .box {
        width: 100%;
        .table-list {
          ol {
            li {
              i {
                right: 230px;
              }
            }
          }
        }
      }
    }
  }
}
