.blog-sidebar {
  padding: 100px 0 60px;
  background-color: #0f0f1d;
  .side-bar {
    margin-top: 40px;
    .side-left {
      .img {
        padding: 0;
        img {
          width: 100%;
        }
      }
      .text {
        padding: 20px 10px 30px 20px;
        background-color: $boxColor;
        margin-bottom: 40px;
        span {
          font-size: 16px;
          padding-right: 10px;
          i {
            color: $PinkColor;
            margin-right: 5px;
          }
        }
        h4 {
          font-size: 21px;
          line-height: 30px;
          color: $WightColor;
          font-weight: bold;
          margin-top: 20px;
          margin-bottom: 20px;
          @include transition;
          &:hover {
            color: $PinkColor;
            cursor: pointer;
          }
        }
        p {
          font-size: 16px;
          font-weight: bold;
          margin-bottom: 30px;
        }
        button {
          font-size: 15px;
          padding: 8px 30px;
        }
      }
      .last-text {
        margin-bottom: 0;
      }
    }
    .side-right {
      form {
        .search {
          margin-bottom: 30px;
          display: block;
          border: 1px solid $PargraphColor;
          border-radius: 3px;
          input {
            padding: 5px 15px;
            color: $PargraphColor;
            width: 80%;
            border: none;
            height: 56px;
            background: transparent;
          }
          button {
            background: transparent;
            border: none;
            i {
              color: $PargraphColor;
              &:hover {
                color: $PinkColor;
              }
            }
          }
        }
      }
      h4 {
        position: relative;
        padding-bottom: 20px;
        margin-top: 50px;
        font-weight: bold;
        &::after {
          content: "";
          position: absolute;
          width: 25%;
          height: 1px;
          background-color: $PinkColor;
          left: 0;
          bottom: 0;
        }
      }
      .Categories {
        ul {
          padding: 0;
          margin: 20px 0 0 0;
          li {
            position: relative;
            margin-bottom: 15px;
            span {
              display: inline-block;
              padding: 3px 5px;
              background-color: $boxColor;
              margin-right: 30px;
              border-radius: 5px;
              font-size: 14px;
              font-weight: bold;
            }
            a {
              color: $PargraphColor;
              text-decoration: none;
              font-weight: bold;
              transition: ease-in-out 0.3s;
              &:hover {
                color: $PinkColor;
              }
            }
            i {
              position: absolute;
              right: 0;
              color: $PargraphColor;
            }
          }
        }
      }
      .Recent {
        .img-text {
          margin-top: 30px;
          .img {
            padding: 0 0 0 15px;
            img {
              width: 100%;
              border-radius: 10px;
              object-fit: cover;
            }
          }
          .text {
            padding: 0 0 0 15px;
            h5 {
              font-size: 17px;
              font-weight: bold;
              transition: ease-in-out 0.3s;
              &:hover {
                color: $PinkColor;
                cursor: pointer;
              }
            }
            span {
              font-size: 15px;
              font-weight: bold;
            }
          }
        }
      }
      .Popular {
        ul {
          padding: 0;
          margin: 20px 0 0 0;
          li {
            display: inline-block;
            margin: 5px 3px;
            padding: 5px 10px;
            width: auto;
            background: $boxColor;
            border: 1px solid $boxColor;
            color: $PargraphColor;
            border-radius: 3px;
            font-weight: 400;
            font-size: 14px;
            font-weight: bold;
            transition: 0.4s;
            &:hover {
              background-color: $PinkColor;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 991px) {
  .blog-sidebar {
    .side-bar {
      .side-right {
        .Recent {
          .img-text {
            .img {
              width: 30%;
              padding-left: 0;
              img {
                width: 100%;
              }
            }
            .text {
              width: 70%;
            }
          }
        }
      }
    }
  }
}
