.Terms-Conditions {
  background-color: $MainColor;
  padding: 100px 0;
  .company-terms {
    margin-top: 60px;
    h3 {
      font-size: 23px;
      font-weight: 500;
      margin-bottom: 10px;
      color: $WightColor;
      .number {
        color: $PinkColor;
        font-weight: 600;
        padding-right: 10px;
        float: left;
      }
      .terms-text {
        padding-left: 40px;
        display: inherit;
        font-size: 23px;
        font-weight: 600;
      }
    }
    p {
      padding-left: 40px;
      font-weight: 600;
      margin: 0 0 15px;
      font-size: 16px;
      line-height: 30px;
    }
  }
}
