.playing {
  background-color: $MainColor;
  padding: 100px 0;
  z-index: 1000;
  .img {
    margin-bottom: 40px;
    img {
      width: 100%;
    }
  }
  .Signup-Today {
    h2 {
      color: $WightColor;
      font-size: 40px;
      line-height: 46px;
      font-weight: bold;
      margin-bottom: 30px;
    }
    p {
      margin-bottom: 15px;
      font-weight: bold;
    }
    ul {
      padding-left: 0;
      margin: 0;
      li {
        position: relative;
        display: block;
        font-weight: bold;
        padding: 6px 0px 6px 35px;
        margin-bottom: 10px;
        &::after {
          position: absolute;
          left: 0px;
          top: 6px;
          content: url("../image/homes/home1/tick.png");
          font-family: themify;
          width: 24px;
          height: 24px;
          line-height: 31px;
          text-align: center;
          border-radius: 50px;
          font-size: 12px;
          color: #fff;
          background: #d5057f;
        }
      }
    }
    button {
      margin-top: 15px;
      text-transform: capitalize;
      font-size: 16px;
    }
  }
}
