.slide {
  background-color: $boxColor;
  padding: 25px 0;
  font-size: 17px;
  margin-right: 3%;
  margin-bottom: 3%;
  width: 30%;
  &:last-of-type {
    margin-right: 0%;
  }
  img {
    margin-bottom: 30px;
    margin-top: 30px;
    width: 160px;
    height: 160px;
  }
  button {
    display: block;
    margin: auto;
  }
}
@media (max-width: 991px) {
  .slide {
    width: 47%;
  }
}
@media (max-width: 769px) {
  .slide {
    width: 100%;
    margin-right: 0;
  }
}
