.breadcrumb {
  background-color: $SecondaryColor;
  .img-back {
    padding: 150px 0 180px 0;
    background-image: url("../image/homes/get-started/bread-bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    z-index: 1;
    &::after {
      background: $SecondaryColor;
      opacity: 0.5;
      position: absolute;
      content: "";
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
    }
  }
  h2 {
    color: $WightColor;
    text-transform: uppercase;
    font-size: 50px;
    line-height: 50px;
    margin-bottom: 30px;
    font-weight: bold;
  }
  a {
    text-decoration: none;
    @include transition;
    &:hover {
      color: $PinkColor;
    }
  }
  i {
    margin: 0 15px;
    color: $WightColor;
    font-size: 14px;
  }
  span {
    color: $WightColor;
    font-weight: bold;
  }
}
