.Games-Details {
  background-color: $MainColor;
  padding: 80px 0;
  .Pirate {
    margin-top: 50px;
    .img {
      img {
        width: 100%;
      }
    }
    .text {
      h4 {
        color: $WightColor;
        font-weight: bold;
      }
      .Rating {
        margin-top: 15px;
        .Rating-num {
          display: inline-block;
          margin-right: 20px;
          font-size: 14px;
          font-weight: bold;
        }
        .stars {
          font-size: 14px;

          i {
            color: $PinkColor;
            cursor: pointer;
            &:last-of-type {
              color: $PargraphColor;
            }
          }
        }
      }
      p {
        margin: 20px 0;
        font-size: 15px;
        font-weight: bold;
      }
    }
  }
  .Games-Operating-System {
    margin-right: 15px;
    margin-left: -15px;
    background-color: $boxColor;
    padding: 40px;
    margin-top: 50px;
    h3 {
      color: $WightColor;
      font-weight: bold;
    }
    > p {
      font-size: 15px;
      margin: 20px 0 50px;
    }
    .boxses {
      width: 100%;
      margin-left: 0px;

      .box {
        width: 49%;
        border: 1px solid #666;
        border-radius: 4px;
        margin-bottom: 2%;
        margin-left: 0;
        padding: 30px;
        &:nth-of-type(odd) {
          margin-right: 2%;
        }
        .unmber {
          width: 15%;
          span {
            display: inline-block;
            background-color: $PinkColor;
            color: $WightColor;
            font-weight: bold;
            font-size: 22px;
            padding: 10px;
            border-radius: 50%;
          }
        }
        .info {
          width: 85%;
          h4 {
            color: $WightColor;
            font-weight: bold;
            margin-bottom: 15px;
          }
          p {
            font-size: 16px;
          }
        }
      }
    }
  }
  .Games-Controller-System {
    background-color: $boxColor;
    padding: 40px;
    margin-top: 50px;
    margin-right: 15px;
    .text {
      h3 {
        color: $WightColor;
        font-weight: bold;
      }
      p {
        font-size: 16px;
      }
      ul {
        padding: 0;
        margin: 0;
        li {
          font-size: 15px;
          margin-top: 10px;
          i {
            margin-right: 20px;
            color: $PinkColor;
            font-size: 14px;
          }
        }
      }
    }
    .img {
      img {
        width: 100%;
      }
    }
  }
}

@media (max-width: 991px) {
  .Games-Details {
    .Games-Operating-System {
      margin-right: -15px;
      .boxses {
        width: 100%;
        .box {
          width: 100%;
          &:nth-of-type(odd) {
            margin-right: 0;
          }
        }
      }
    }
    .Games-Controller-System {
      margin-right: -15px;
      .text {
        margin-bottom: 40px;
      }
    }
  }
}
