.Games-Partner {
  background-color: $boxColor;
  padding: 80px;
  overflow: hidden;
  position: relative;
  border-radius: 4px;
  top: 200px;
  .head-parg {
    p {
      margin: 0 225px !important;
    }
  }
  .companys {
    margin-top: 40px;
    .imgs {
      img {
        padding: 15px 20px;
        cursor: pointer;
      }
    }
  }
}

@media (max-width: 991px) {
  .Games-Partner {
    .head-parg {
      p {
        margin: 0 50px !important;
      }
    }
    .companys {
      .imgs {
        img {
          padding: 15px 20px;
        }
      }
    }
  }
}
@media (max-width: 769px) {
  .Games-Partner {
    .head-parg {
      h2 {
        font-size: 20px;
      }
      p {
        margin: 0 20px !important;
        font-size: 13px;
      }
    }
    .companys {
      .imgs {
        margin: auto;
        img {
          padding: 0 0 0 10px;
          width: 20%;
        }
      }
    }
  }
}
