* {
  box-sizing: border-box;
}
body {
  color: #c4d5f6;
  font-family: "Nunito", sans-serif;
  font-size: 17px;
  text-align: left;
  overflow-x: hidden;
  line-height: 26px;
  background-color: $SecondaryColor;
}
ul {
  list-style: none;
}
.active {
  background-color: $PinkColor !important;
}
input:focus,
button:focus,
textarea:focus {
  outline: none;
}
.footer-with-padding {
  padding-top: 190px;
  background-color: #0d0b17;
}
img {
  cursor: pointer;
}
@media (max-width: 575px) {
  .row {
    margin-left: 0;
    margin-right: 0;
  }
}
