.button {
  padding: 10px 20px;
  font-weight: bold;
  text-transform: uppercase;
  border: 1px solid $PinkColor;
  border-radius: 4px;
  background-color: $PinkColor;
  color: $WightColor;
  @include transition;
  &:hover {
    background-color: $SecondaryColor;
    color: $PinkColor;
  }
}
