.numbers {
  color: $WightColor;
  margin-top: 80px;
  .achive {
    background-color: $boxColor;
    padding: 35px 0;
    margin-right: 30px;
    border-radius: 5px;
    width: 32%;
    &:last-of-type {
      margin-right: 0;
    }
    img {
      border-radius: 50%;
    }
    .num {
      margin-top: 15px;
      font-weight: bold;
      .numplus {
        font-size: 36px;
        margin-bottom: 10px;
      }
      .text {
        font-size: 13px;
        margin-left: 25px;
        position: relative;
        &::before {
          content: "";
          position: absolute;
          width: 10px;
          height: 10px;
          border-radius: 50%;
          top: 8px;
          left: -20px;
        }
      }
      .txt1::before {
        background-color: green;
      }
      .txt2::before {
        background-color: yellow;
      }
      .txt3::before {
        background-color: $PinkColor;
      }
    }
  }
}
@media (max-width: 991px) {
  .numbers {
    .achive {
      width: 70%;
      margin: auto auto 30px auto;
      img {
        width: 25%;
      }
      .num {
        width: 75%;
        margin-top: 20px;
      }
      &:last-of-type {
        margin-right: auto;
      }
    }
  }
}
@media (max-width: 575px) {
  .numbers {
    .achive {
      img {
        width: 40%;
      }
      .num {
        width: 60%;
      }
    }
  }
}
