.Error {
  color: $WightColor;
  background-color: $MainColor;
  text-align: center;
  padding: 100px 0;
  h2 {
    font-weight: bold;
  }
  h1 {
    font-size: 230px;
    line-height: 230px;
    font-weight: bold;
    margin: 20px 0;
    .color {
      color: $PinkColor;
    }
  }
  p {
    color: $PargraphColor;
    font-size: 27px;
    font-weight: bold;
    margin-bottom: 70px;
  }
  a {
    text-decoration: none;
    padding: 12px 20px;
  }
}
