.how-to-start {
  padding: 100px 0;
  font-weight: bold;
  .boxses {
    margin-top: 120px;
    .box {
      background-color: $boxColor;
      width: 30%;
      padding: 30px 30px 20px 30px;
      text-align: center;
      border-radius: 4px;
      margin-right: 3%;
      &:last-of-type {
        margin-right: 0;
      }
      &:nth-of-type(2) {
        position: relative;
        top: -60px;
      }

      .img {
        position: relative;
        width: 120px;
        height: 120px;
        margin: auto;
        .h-numbr {
          width: 40px;
          height: 40px;
          line-height: 40px;
          font-size: 16px;
          color: $WightColor;
          border-radius: 50%;
          position: absolute;
          left: -10px;
          top: -10px;
          background: $PinkColor;
        }
      }
      .text {
        h4 {
          color: $WightColor;
          font-weight: bold;
          margin: 20px 15px 15px 15px;
        }
        p {
          font-size: 16px;
        }
      }
    }
  }
}

@media (max-width: 991px) {
  .how-to-start {
    .boxses {
      .box {
        width: 100%;
        margin-bottom: 3%;
        margin-right: 0;
        &:nth-of-type(2) {
          position: relative;
          top: 0;
        }
        .text {
          p {
            padding-right: 90px;
            padding-left: 90px;
          }
        }
      }
    }
  }
}

@media (max-width: 769px) {
  .how-to-start {
    .boxses {
      .box {
        .text {
          p {
            padding-right: 0px;
            padding-left: 0px;
          }
        }
      }
    }
  }
}
