/* Start Top Bar */
.sticky {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
}
.top-bar {
  background-color: $SecondaryColor;
  color: $PargraphColor;
  padding: 10px 0;
  .left-area {
    .emil {
      margin-right: 25px;
      cursor: pointer;
      i {
        margin-right: 10px;
      }
    }
    .live {
      cursor: pointer;
    }
  }
  .right-area {
    position: relative;
    padding-right: 0;
    margin-right: -15px;
    .lang {
      cursor: pointer;
      position: absolute;
      right: 60px;
      i {
        margin-left: 8px;
      }
      .sub-menu {
        position: absolute;
        width: 150px;
        padding: 15px;
        background-color: $MainColor;
        z-index: 10;
        display: none;
        font-size: 12px;
        a {
          color: $WightColor;
          display: inline-block;
          margin-bottom: 7px;
          text-decoration: none;
          img {
            margin-right: 5px;
            margin-top: -3px;
          }
        }
      }
      &:hover {
        .sub-menu {
          display: block;
        }
      }
    }
    .login {
      cursor: pointer;
      position: absolute;
      right: -15px;
      a {
        color: $PargraphColor;
        font-weight: bold;
        text-decoration: none;
      }
    }
  }
}

/* Start Logo Bar */
.logo-bar {
  background-color: $navbarColor;
  .container {
    position: relative;
  }
  .row {
    margin-right: 0;
  }
  .logo {
    padding: 10px 0;
    img {
      cursor: pointer;
    }
  }
  .links {
    text-transform: uppercase;
    color: $WightColor;
    position: relative;
    ul {
      list-style: none;
      padding-left: 0;
      padding-right: 100px;
      margin: 0;
      text-align: center;
      li {
        display: inline-block;
        padding: 24px 12px;
        font-weight: bold;
        cursor: pointer;
        @include transition;
        &:hover {
          .link {
            display: block;
          }
          > a {
            color: $PinkColor;
            &::after {
              width: 70%;
            }
          }
        }
        > a {
          @include transition;
          color: $WightColor;
          position: relative;
          &:hover {
            color: $PinkColor;
            text-decoration: none;
          }
          &::after {
            content: "";
            position: absolute;
            width: 0;
            height: 2px;
            background-color: $PinkColor;
            top: 23px;
            left: 3px;
            @include transition;
          }
          &:hover::after {
            width: 70%;
          }
        }
        .link {
          position: absolute;
          width: 220px;
          padding: 5px 20px 15px 20px;
          top: 73px;
          border-radius: 3px;
          background-color: #272544;
          z-index: 10;
          font-size: 14px;
          display: none;
          text-align: start;
          a {
            color: $WightColor;
            display: block;
            margin-bottom: 10px;
            text-decoration: none;
            @include transition;
            &:hover {
              color: $PinkColor;
            }
          }
        }
      }
    }
    .button {
      margin-left: 20px;
      margin-right: 15px;
      text-decoration: none;
      position: absolute;
      right: 0;
      top: 10px;
      display: inline-block;
    }
  }
}

@media (max-width: 991px) {
  .top-bar {
    height: 100px;
    .left-area {
      width: 100%;
    }
    .right-area {
      margin-top: 20px;
      .lang {
        left: 15px;
        margin-right: 20px;
      }
      .login {
        left: 130px;
      }
    }
  }
  .logo-bar {
    .links {
      display: none;
    }
  }
}

.menu {
  position: absolute;
  right: 0px;
  font-size: 30px;
  display: none;
  cursor: pointer;
  padding: 15px 0;
  transition: ease-in-out 0.4s;
  &:hover {
    color: $PinkColor;
  }

  i {
    margin-top: 5px;
  }
}
@media (max-width: 991px) {
  .menu {
    display: block;
    z-index: 1000;
  }
}

@media (max-width: 575px) {
  .top-bar {
    .left-area {
      padding: 0;
    }
    .right-area {
      padding: 0;
      .lang {
        left: 0px;
      }
      .login {
        left: 110px;
      }
    }
  }
  .menu {
    padding-right: 15px;
  }
}

.menu-links {
  background-color: $boxColor;
  width: 250px;
  margin-right: -250px;
  height: 100vh;
  padding-top: 90px;
  position: fixed;
  top: 0;
  right: 0;
  overflow: auto;
  z-index: 10000000;
  transition: ease-in-out 0.4s;

  > i {
    position: absolute;
    right: 15px;
    top: 15px;
    font-size: 25px;
    display: block;
    transition: ease-in-out 0.3s;
    &:hover {
      color: $PinkColor;
      cursor: pointer;
    }
  }
  .links {
    ul {
      padding: 0;
      margin: 0;
      .li {
        display: flex;
        flex-direction: row;
        position: relative;
        transition: ease-in-out 0.3s;
        &:hover {
          background-color: #1e1935;
        }
        li {
          width: 90%;
        }
        .icon {
          width: 10%;
          position: relative;
          border-bottom: 1px solid #1e1935;
          cursor: pointer;
          transition: ease-in-out 0.3s;
          &:hover {
            color: $PinkColor;
          }
          i {
            position: absolute;
            top: 20px;
          }
          .rotate {
            transform: rotate(90deg);
          }
        }
      }
      .backgrlink {
        background-color: #1e1935;
      }
      li {
        padding: 15px 0 15px 30px;
        position: relative;
        border-bottom: 1px solid #1e1935;
        transition: ease-in-out 0.3s;
        &:hover {
          background-color: #1e1935;
        }
        a {
          color: $PargraphColor;
          display: block;
          text-decoration: none;
          transition: ease-in-out 0.3s;
          &:hover {
            color: $PinkColor;
          }
        }
        .link {
          display: none;
          margin-top: 15px;
          a {
            padding-left: 20px;
            padding-top: 5px;
            font-size: 13px;
            padding-bottom: 5px;
            &:hover {
              background-color: $boxColor;
              color: #fff;
            }
          }
        }
        .hidden {
          display: block;
        }
      }
    }
  }
}
