.about-our-work {
  margin-top: 100px;
  padding: 80px 0;
  margin-right: -25px;
  background-color: $MainColor;
  width: 100%;
  .info-box {
    background-color: $boxColor;
    margin-bottom: 30px;
    padding: 20px 20px 15px 20px;
    border-radius: 4px;
    width: 48%;
    .img {
      padding: 0;
      border-radius: 50%;
      img {
        width: 80px;
        height: 80px;
        border-radius: 50%;
      }
    }
    .text {
      padding: 0 0 0 15px;
      h4 {
        color: #fff;
        font-weight: bold;
      }
    }
  }
  .margin-right {
    margin-right: 4%;
  }
}
@media (max-width: 991px) {
  .about-our-work {
    .info-box {
      width: 100%;
      margin-left: 0px;
    }
    .margin-right {
      margin-right: 0%;
    }
  }
}
@media (max-width: 575px) {
  .about-our-work {
    .info-box {
      .img {
        width: 20%;
      }
      .text {
        width: 80%;
      }
    }
  }
}
