.games-playe {
  padding-top: 80px;
  padding-bottom: 80px;
  margin-top: 40px;
  background-color: $MainColor;
  .boxses {
    margin-top: 50px;
    padding-left: 0;
    width: 100%;
    font-weight: bold;
    li {
      background-color: $boxColor;
      width: 32%;
      padding: 20px 0;
      margin-right: 15px;
      cursor: pointer;
      &:last-of-type {
        margin-right: 0;
      }
      a {
        color: $WightColor;
        text-decoration: none;
        font-size: 22px;
      }
    }
  }
  .users-img {
    margin-top: 40px;
    .users {
      .user {
        background-color: $boxColor;
        color: $WightColor;
        margin-bottom: 20px;
        padding: 0px;
        border-radius: 5px;
        .img {
          border-radius: 50%;
          margin-top: 5px;
          img {
            width: 100%;
            border-radius: 50%;
          }
        }
        .text {
          h5 {
            font-size: 15px;
            font-weight: bold;
            margin: 10px 0 5px 0;
          }
          p {
            font-size: 15px;
            font-weight: bold;
            color: $PargraphColor;
          }
        }
      }
    }
    .img {
      position: relative;
      height: 100%;
      img {
        width: 100%;
      }
      button {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%);
        opacity: 0.8;
        &:hover {
          opacity: 1;
          background-color: $PinkColor;
          color: $WightColor;
        }
      }
    }
  }
}
@media (max-width: 991px) {
  .games-playe {
    .boxses {
      li {
        width: 100%;
        margin-right: 0;
        margin-bottom: 30px;
      }
    }
    .users-img {
      .users {
        .user {
          margin-right: 15px;
          .img {
            width: 12%;
          }
          .text {
            width: 80%;
          }
        }
      }
    }
  }
}
@media (max-width: 769px) {
  .games-playe {
    .users-img {
      .users {
        padding: 0;
        .user {
          margin-right: 0;
          .img {
            width: 20%;
          }
          .text {
            width: 80%;
          }
        }
      }
    }
  }
}
