.head-parg {
  text-align: center;
  .head {
    font-size: 40px;
    text-transform: capitalize;
    line-height: 48px;
    color: $WightColor;
    font-weight: bold;
  }
  p {
    margin: 0 270px;
    font-size: 17px;
    font-weight: bold;
  }
}
@media (max-width: 991px) {
  .head-parg {
    p {
      margin: 0 50px;
    }
  }
}
