.Important-FAQ {
  padding: 80px 0;
  background-color: $MainColor;
  .Accordion-dad {
    margin-top: 60px;
    .header {
      padding-right: 40px;
      h4 {
        color: $WightColor;
        font-weight: bold;
        padding: 15px;
        background-color: $boxColor;
        margin-bottom: 40px;
        border-radius: 5px;
      }
      .accordion {
        background-color: $boxColor;
        color: $WightColor;
        cursor: pointer;
        padding: 20px;

        width: 100%;
        border: none;
        text-align: left;
        outline: none;
        font-size: 18px;
        font-weight: bold;
        transition: 0.4s;
        &:after {
          content: "\002B";
          color: $WightColor;
          font-weight: bold;
          float: right;
          margin-left: 5px;
        }
      }
      .open {
        &:after {
          content: "\2212";
        }
      }
      .panel {
        padding: 0 15px;
        background-color: $boxColor;
        color: $PargraphColor;
        font-weight: bold;
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.2s ease-out;
        margin-bottom: 15px;
      }
    }
  }
  .box {
    padding: 25px;
    background-color: $boxColor;
    height: max-content;
    border-radius: 5px;
    h2 {
      color: $WightColor;
      font-weight: bold;
    }
    p {
      color: $WightColor;
      font-weight: bold;
      font-size: 16px;
    }
  }
}

@media (max-width: 991px) {
  .Important-FAQ {
    .Accordion-dad {
      .header {
        padding-left: 0;
        padding-right: 0;
      }
    }
  }
}
