.Games-World-News {
  padding: 100px 0 0 0;
  .boxses {
    margin-top: 40px;
    .box {
      margin-bottom: 20px;
      .img {
        width: 100%;
        padding: 0;
        img {
          width: 100%;
        }
      }
      .text {
        padding: 25px;
        background-color: $boxColor;
        span {
          font-size: 14px;
          padding-right: 10px;
          i {
            color: $PinkColor;
            margin-right: 5px;
          }
        }
        h4 {
          font-size: 21px;
          line-height: 30px;
          color: $WightColor;
          font-weight: bold;
          margin-top: 20px;
          @include transition;
          &:hover {
            color: $PinkColor;
            cursor: pointer;
          }
        }
      }
    }
  }
}
