.blog-Details {
  padding: 100px 0 60px;
  background-color: $MainColor;
  .side-bar {
    .side-left {
      .img {
        padding: 0;
        margin-bottom: 30px;
        img {
          width: 100%;
        }
      }
      .text-deta {
        padding: 0;
        line-height: 32px;
        span {
          font-size: 16px;
          padding-right: 10px;
          i {
            color: $PinkColor;
            margin-right: 5px;
          }
        }

        h4 {
          color: $WightColor;
          font-weight: bold;
          margin: 20px 0;
        }
        .box-parg {
          background-color: $boxColor;
          padding: 20px 40px 40px 80px;
          margin: 0 0 20px;
          font-size: 16px;
          font-weight: bold;
          border-left: none;
          font-style: italic;
          position: relative;
          i {
            position: absolute;
            top: 20px;
            left: 30px;
            font-size: 34px;
            color: #c4d5f6;
            font-style: italic;
          }
        }
        p {
          font-weight: bold;
        }
      }
      .text-parg {
        padding: 0;
        p {
          font-weight: bold;
          margin-bottom: 80px;
        }
      }
      .Related-Post {
        h3 {
          border-bottom: 1px solid $boxColor;
          color: #c4d5f6;
          font-size: 20px;
          font-weight: bold;
          margin: 40px 0;
          padding: 0 0 10px;
          text-transform: capitalize;
        }
        .Posts {
          margin-left: 0px;
          .img-text {
            width: 48%;
            margin-right: 2%;
            margin-top: 15px;
            margin-left: 0px;
            .img {
              img {
                width: 100%;
                border-radius: 10px;
                object-fit: cover;
              }
            }
            .text {
              padding: 0 0 0 15px;
              h5 {
                font-size: 17px;
                font-weight: bold;
                transition: ease-in-out 0.3s;
                &:hover {
                  color: $PinkColor;
                  cursor: pointer;
                }
              }
              span {
                font-size: 15px;
                font-weight: bold;
              }
            }
          }
        }
      }
      .comments {
        margin-left: 0;
        h3 {
          border-bottom: 1px solid $boxColor;
          color: #c4d5f6;
          font-size: 20px;
          font-weight: bold;
          margin: 40px 0;
          padding: 0 0 10px;
          text-transform: capitalize;
        }
        .user {
          margin-bottom: 30px;
          margin-left: 0;
          .img {
            width: 15%;
            margin-right: 2.5%;
            border-radius: 5px;
            img {
              width: 100%;
              border-radius: 5px;
            }
          }
          .text {
            width: 80%;
            margin-top: 15px;
            .date {
              .color {
                color: $PinkColor;
                cursor: pointer;
                font-weight: bold;
              }
            }
            p {
              margin-top: 10px;
              font-weight: bold;
              font-size: 15px;
            }
          }
        }
        .user-marg {
          margin-left: 70px;
        }
      }
      .Reply {
        margin-left: 0;
        h3 {
          border-bottom: 1px solid $boxColor;
          color: #c4d5f6;
          font-size: 20px;
          font-weight: bold;
          margin: 40px 0;
          padding: 0 0 10px;
          text-transform: capitalize;
        }
        > span {
          font-size: 14px;
        }
        form {
          margin-top: 30px;
          .row {
            margin-left: 0;
          }
          .f-name {
            width: 31%;
            margin-right: 3%;
            p {
              margin-bottom: 5px;
              font-weight: bold;
            }
            input {
              border: 1px solid $boxColor;
              border-radius: 0;
              height: 46px;
              margin-bottom: 15px;
              padding: 0 0 0 10px;
              width: 100%;
              background: transparent;
              color: $PargraphColor;
            }
          }
          .l-name {
            width: 32%;
            margin-right: 3%;
            p {
              margin-bottom: 5px;
              font-weight: bold;
            }
            input {
              border: 1px solid $boxColor;
              border-radius: 0;
              height: 46px;
              margin-bottom: 15px;
              padding: 0 0 0 10px;
              width: 100%;
              background: transparent;
              color: $PargraphColor;
            }
          }
          .email {
            width: 31%;
            p {
              margin-bottom: 5px;
              font-weight: bold;
            }
            input {
              border: 1px solid $boxColor;
              border-radius: 0;
              height: 46px;
              margin-bottom: 15px;
              padding: 0 0 0 10px;
              width: 100%;
              background: transparent;
              color: $PargraphColor;
            }
          }
          .Massage {
            width: 100%;
            p {
              margin-bottom: 5px;
              font-weight: bold;
            }
            textarea {
              border: 1px solid $boxColor;
              border-radius: 0;
              padding: 10px;
              height: 200px;
              width: 100%;
              background: transparent;
            }
            input {
              background: $PinkColor;
              color: $WightColor;
              display: block;
              font-size: 16px;
              margin-top: 5px;
              padding: 10px 20px;
              transition: 0.4s;
              border-radius: 2px;
              width: auto !important;
              margin-top: 20px;
              border: 1px solid #d5057f;
              text-transform: capitalize;
              font-weight: 700;
              transition: 0.4s;
              &:hover {
                color: $PinkColor;
                background-color: transparent;
              }
            }
          }
        }
      }
    }
    .side-right {
      form {
        .search {
          margin-bottom: 30px;
          display: block;
          border: 1px solid $PargraphColor;
          border-radius: 3px;
          input {
            padding: 5px 15px;
            color: $PargraphColor;
            width: 80%;
            border: none;
            height: 56px;
            background: transparent;
          }
          button {
            background: transparent;
            border: none;
            i {
              color: $PargraphColor;
              &:hover {
                color: $PinkColor;
              }
            }
          }
        }
      }
      h4 {
        position: relative;
        padding-bottom: 20px;
        margin-top: 50px;
        font-weight: bold;
        &::after {
          content: "";
          position: absolute;
          width: 25%;
          height: 1px;
          background-color: $PinkColor;
          left: 0;
          bottom: 0;
        }
      }
      .Categories {
        ul {
          padding: 0;
          margin: 20px 0 0 0;
          li {
            position: relative;
            margin-bottom: 15px;
            span {
              display: inline-block;
              padding: 3px 5px;
              background-color: $boxColor;
              margin-right: 30px;
              border-radius: 5px;
              font-size: 14px;
              font-weight: bold;
            }
            a {
              color: $PargraphColor;
              text-decoration: none;
              font-weight: bold;
              transition: ease-in-out 0.3s;
              &:hover {
                color: $PinkColor;
              }
            }
            i {
              position: absolute;
              right: 0;
              color: $PargraphColor;
            }
          }
        }
      }
      .Recent {
        .img-text {
          margin-top: 30px;
          .img {
            padding: 0 0 0 15px;
            img {
              width: 100%;
              border-radius: 10px;
              object-fit: cover;
            }
          }
          .text {
            padding: 0 0 0 15px;
            h5 {
              font-size: 17px;
              font-weight: bold;
              transition: ease-in-out 0.3s;
              &:hover {
                color: $PinkColor;
                cursor: pointer;
              }
            }
            span {
              font-size: 15px;
              font-weight: bold;
            }
          }
        }
      }
      .Popular {
        ul {
          padding: 0;
          margin: 20px 0 0 0;
          li {
            display: inline-block;
            margin: 5px 3px;
            padding: 5px 10px;
            width: auto;
            background: $boxColor;
            border: 1px solid $boxColor;
            color: $PargraphColor;
            border-radius: 3px;
            font-weight: 400;
            font-size: 14px;
            font-weight: bold;
            transition: 0.4s;
            &:hover {
              background-color: $PinkColor;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 991px) {
  .blog-Details {
    .side-bar {
      .side-left {
        .Related-Post {
          .Posts {
            .img-text {
              .img {
                width: 30%;
              }
              .text {
                width: 70%;
              }
            }
          }
        }
        .Reply {
          form {
            margin-bottom: 50px;
          }
        }
      }
      .side-right {
        .Recent {
          .img-text {
            .img {
              width: 30%;
              padding-left: 0;
            }
            .text {
              width: 70%;
            }
          }
        }
      }
    }
  }
}
