.get-started-3 {
  background-image: url("../image/homes/get-started/s1.png");
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  padding: 130px 0;
  position: relative;
  margin-bottom: -80px;
  z-index: 1;
  &::after {
    background: $SecondaryColor;
    opacity: 0.9;
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  h2 {
    font-size: 65px;
    font-weight: bold;
    color: $WightColor;
    span {
      color: $PinkColor;
    }
  }
  p {
    margin: 30px 0 50px 0;
    font-weight: bold;
    font-size: 20px;
    line-height: 30px;
  }
}

.vedio {
  background-color: $MainColor;
  padding-top: 160px;
  margin-top: -80px;
  margin-bottom: -160px;
  img {
    width: 100%;
    margin: auto;
  }
}

@media (max-width: 991px) {
  .get-started-3 {
    margin-bottom: 0;
    padding: 100px 0;
  }
  .vedio {
    margin-top: 30px;
  }
}
