.getStarted {
  background-image: url("../image/homes/get-started/slide-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 80px 0;
  .img-area {
    img {
      width: 100%;
    }
  }
  .text-area {
    padding-left: 0;
    padding-top: 50px;
    h2 {
      font-size: 48px;
      font-weight: bold;
      color: $WightColor;
      span {
        color: $PinkColor;
      }
    }
    p {
      margin-top: 30px;
      margin-bottom: 40px;
      font-weight: bold;
    }
  }
}
@media (max-width: 991px) {
  .getStarted {
    .img-area {
      display: none;
    }
  }
}
