.footer {
  background-color: #0d0b17;
  padding: 90px 0 90px;
  .logo {
    margin-top: -10px;
    .img-logo {
      margin-bottom: 25px;
    }
    p {
      font-size: 16px;
      margin-bottom: 33px;
      line-height: 27px;
    }
    .input-button {
      width: 100%;
      padding-left: 15px;
      input {
        background-color: transparent;
        border: 1px solid #666;
        border-right: none;
        padding: 6px 15px;
        color: $PargraphColor;
        &::placeholder {
          font-size: 16px;
          color: #757b82;
        }
      }
      button {
        text-align: center;
        font-size: 14px;
        border-radius: 0;
        padding: 10px 5px;
        &:hover {
          color: $PinkColor;
        }
      }
    }
  }
  h4 {
    color: $WightColor;
    font-weight: bold;
    font-size: 20px;
    margin-bottom: 30px;
  }
  a {
    display: block;
    color: $PargraphColor;
    font-weight: bold;
    text-decoration: none;
    margin-bottom: 10px;
    font-size: 16px;
    transition: ease-in-out 0.15s;
    &:hover {
      color: $PinkColor;
    }
  }
  .padding {
    padding: 0 15px;
  }
}

.copyright {
  background-color: $boxColor;
  color: $PargraphColor;
  padding: 25px 0;
  font-size: 16px;
  font-weight: bold;
  p {
    margin: 0;
  }
  span {
    color: $PinkColor;
  }
}

@media (max-width: 991px) {
  .footer {
    .logo,
    .GAMES-LIST,
    .COMPANY,
    .ACCOUNT,
    .SUPPORT {
      margin-bottom: 40px;
    }
    .GAMES-LIST,
    .COMPANY,
    .ACCOUNT,
    .SUPPORT {
      text-align: center;
    }
  }
  .copyright {
    text-align: center;
  }
}
@media (max-width: 769px) {
  .footer {
    .GAMES-LIST,
    .COMPANY,
    .ACCOUNT,
    .SUPPORT {
      text-align: left;
    }
  }
  .copyright {
    text-align: left;
  }
}
@media (max-width: 575px) {
  .footer {
    .logo {
      .input-button {
        padding: 0;
        input {
          width: 70%;
        }
        button {
          width: 30%;
        }
      }
    }
  }
}
